<template>
  <div>
    <component
      v-for="question in arrayQuestions"
      :key="question.id"
      :is="`File${question.type[0].toUpperCase() + question.type.slice(1)}`"
      :questionsTypes="`File${
        question.type[0].toUpperCase() + question.type.slice(1)
      }`"
      v-model="$store.state.savedAnswers[question.id]"
      :question-number="question.questionNumber"
      :title="question.title"
      :description="question.description"
      :comments="question.comments"
      :img="question.img"
      :required="question.required"
      :questionDifficult="question.questionDifficult"
      @finish="$store.commit('increment')"
      @clean="$store.commit('decrease')"
    ></component>
  </div>
</template>

<script>
import FileSimple from "./file/FileSimple.vue";
import { getFile } from "@/fake/api";

export default {
  data() {
    return {
      arrayQuestions: [],
    };
  },
  components: { FileSimple },

  mounted() {
    this.arrayQuestions = getFile();
    this.$emit("setNumberQuestions", this.arrayQuestions.length);
  },
};
</script>
